import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";

/**
 * Generated class for the ChangePin02Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-change-pin02',
  templateUrl: 'change-pin02.html',
})
export class ChangePin02Page {

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public gaProvider : GoogleAnalyticsProvider) {
  }

  goToWelCome = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.popToRoot();
  };

  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Change TPIN Successful");
  };

}



